import {
	mapState,
	mapMutations
} from 'vuex'
import ContactInfo from "@/components/ContactInfo.vue"
import ContactHistory from "@/components/ContactHistory.vue"
import Questionnaire from "@/components/Questionnaire.vue"

export default {
	components: {
		ContactInfo,
		ContactHistory,
		Questionnaire
	},
	data() {
		return {
			star: '',
			phone: '', //电话号码
			province: '', //省份
			city: '', //城市
			phoneCode: '', //地区编号
			campaignId: '',
			id: '',
			customInfoObj: '', //客户信息
			show: 'U', // 默认显示联络历史
			yes: 'N', //服务结果
			serveIndex: ' ', //服务类型下标
			serveArray: ['满意', '不满意'], //服务类型
			date: '', //预约时间默认年月日
			time: '',//预约时间默认时分秒
			questionnaireId: '',
			questionData: {}, //问卷信息
			typeCode: '',
			picketData: [],
			picket_item_2: 0,
			multiIndex: [0, 0, 0], // 服务类型下标
			ajaxobj_rs: {},
			questionObj:{},
			lastConnectTime:'' //最后一次拨打电话时间
		}
	},

	// 提取存储数据
	computed: mapState(['hasLogin', 'uerInfo']),

	onLoad(options) {

		var self = this
		// 上页面传过来的值
		this.campaignId = options.campaignId
		this.id = options.id
		this.phone = options.phone
		this.questionnaireId = options.questionnaireId
		this.typeCode = options.serviceType

		this.getValue('U')
		
		// 获取归属地
		uni.request({
				method: 'GET',
				url: this.apiDomian + '/phoneArea/getRegion',
				data: {
					phoneArea: self.phone,
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					'Authorization': 'Arch6WithCloud ' + self.uerInfo.token
				}
			})
			.then((data) => {
				// 请求后
				var rs = data[1].data.data
				self.province = rs.province
				self.city = rs.city
				self.phoneCode = rs.phoneCode
			})

		// 获取客户信息
		uni.request({
				method: 'GET',
				url: this.apiDomian + '/batchTask/info',
				data: {
					campaignId: self.campaignId,
					id: self.id,
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					'Authorization': 'Arch6WithCloud ' + self.uerInfo.token
				}
			})
			.then((data) => {
				// 请求后
				this.customInfoObj = data[1].data.data
			})

		// 获取服务类型
		uni.request({
				method: 'GET',
				url: this.apiDomian + '/serviceType/findTreeByCode',
				data: {
					typeCode: self.typeCode
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					'Authorization': 'Arch6WithCloud ' + self.uerInfo.token
				}
			})
			.then((data) => {
				var ajaxobj = data[1].data.data.children
				self.ajaxobj_rs = ajaxobj
				var ajaxarr1 = self.getPicketData(ajaxobj)
				var ajaxarr2 = []
				var ajaxarr3 = []
				if(ajaxobj[0].children){
					ajaxarr2 = self.getPicketData(ajaxobj[0].children)
					if(ajaxobj[0].children[0]){ 
						ajaxarr3 = self.getPicketData(ajaxobj[0].children[0].children)
					}
				}

				var temp_arr = []
				temp_arr.push(ajaxarr1)
				temp_arr.push(ajaxarr2)
				temp_arr.push(ajaxarr3)
				self.picketData = temp_arr
			})

	},

	methods: {
		
		//获取年月日时分秒加四位随机数 sessionId
		getLastimes(){
			var data = new Date();
			var year = data.getFullYear();
			var month = data.getMonth() + 1;
			month = month < 10 ? "0" + month : month;
			var date = data.getDate();
			var hour = data.getHours() < 10 ? "0" + data.getHours() : data.getHours();
			var minute = data.getMinutes() < 10 ? "0" + data.getMinutes() : data.getMinutes();
			var second = data.getSeconds() < 10 ? "0" + data.getSeconds() : data.getSeconds();
			var lastConnectTime = (year +'-'+ month +'-'+ date +' '+ hour +':'+ minute +':'+ second)
			this.lastConnectTime = lastConnectTime
		},
		
		//获取服务类型--预约时间默认年月日
		getDatimes(){
			var data = new Date();
			var year = data.getFullYear();
			var month = data.getMonth() + 1;
			month = month < 10 ? "0" + month : month;
			var date = data.getDate();
			var lastConnectTime = (year +'-'+ month +'-'+ date)
			this.date = lastConnectTime
		},
		
		//获取服务类型--预约时间默认时分秒
		getTimes(){
			var data = new Date();
			var hour = data.getHours() < 10 ? "0" + data.getHours() : data.getHours();
			var minute = data.getMinutes() < 10 ? "0" + data.getMinutes() : data.getMinutes();
			var second = data.getSeconds() < 10 ? "0" + data.getSeconds() : data.getSeconds();
			var lastConnectTime = (hour +':'+ minute +':'+ second)
			this.time = lastConnectTime
		},
		
		// 服务类型的三级联动
		picketChange(e){
			
			var val = e.detail.value
			if(e.detail.column == '0'){
				this.picket_item_2 = val
				var item1 = this.picketData[0]
				var item2 =  this.getPicketData(this.ajaxobj_rs[val].children)
				var item3 = []
				if(this.ajaxobj_rs[val].children[0]){
					item3 =  this.getPicketData(this.ajaxobj_rs[val].children[0].children)
				}

				this.picketData = []
				this.picketData.push(item1)
				this.picketData.push(item2)
				this.picketData.push(item3)
				
				this.multiIndex[0] = val
			}else if(e.detail.column == '1'){
				var item1 = this.picketData[0]
				var item2 =  this.picketData[1]
				var item3 =  this.getPicketData(this.ajaxobj_rs[this.picket_item_2].children[val].children)
				this.picketData = []
				this.picketData.push(item1)
				this.picketData.push(item2)
				this.picketData.push(item3)
				this.multiIndex[1] = val
			}else if(e.detail.column == '2'){
				var item1 = this.picketData[0]
				var item2 =  this.picketData[1]
				var item3 = this.picketData[2]
				this.picketData.push(item1)
				this.picketData.push(item2)
				this.picketData.push(item3)
				this.multiIndex[2] = val
			}
		},
		
		// 获取ajax三级联动的数据
		getPicketData(obj){
			if(!obj) return
			var ajaxarr = [];
			for (var i in obj) {
				var temp = {}
				temp.id = obj[i].id
				temp.name = obj[i].typeName
				ajaxarr.push(temp); 
			}
			return ajaxarr
		},
		
		changeNav(key) {
			this.show = key
			this.getValue(key)
		},

		// 获取列表中的数据
		getValue(key) {
			if (key == 'H') {
				var self = this
				uni.request({
					method: 'GET',
					url: this.apiDomian + '/exam/' + self.questionnaireId,
					data: {},
					header: {
						'content-type': 'application/x-www-form-urlencoded',
						'Authorization': 'Arch6WithCloud ' + self.uerInfo.token
					}
				})
				.then((data) => {
					// 请求后
					var obj = data[1].data.data
					this.questionData = obj
				})

			}
			// 	else if (key == 'U') {
			// 		var self = this
			// 		uni.request({  
			// 		    method: 'GET',
			// 		    url: this.apiDomian+'/batchTask/agentOrderPage',  
			// 		    data:  {
			// 				userId: self.uerInfo.id,
			// 				current:'1',
			// 				size:'10',
			// 				descs:'order_time'
			// 			},
			// 		    header: {
			// 		        'content-type': 'application/x-www-form-urlencoded',
			// 				'Authorization': 'Arch6WithCloud '+self.uerInfo.token
			// 		    }
			// 		})
			// 		.then((data) => {
			// 			// 请求后
			// 			console.log(data[1].data.data.records)
			// 			var obj = data[1].data.data.records
			// 			this.customData = obj
			// 		})
			// 
			// 	}

		},
		
		// 拨打电话获取问卷信息
		toCall() {
			var self = this
			
			// 获取拨打时间
			this.getLastimes()
			
			uni.request({
				method: 'GET',
				url: this.apiDomian + '/examSubject/next/' + self.questionnaireId+'/'+ 0,
				data: {},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					'Authorization': 'Arch6WithCloud ' + self.uerInfo.token
				}
			})
			.then((data) => {
				// 请求后
				var Obj = data[1].data.data
				this.questionObj = Obj
			})
		},
		
		// 提交表单数据
		formSubmit() {
			var self = this
			var post_data = {
				callType: 9, //呼叫方向（9-手动外呼）固定写
				connectStatus: 'N', //联系状态，Y-接通；N-未接通，后期需要软电话接口判断
				id: this.id,
				lastConnectTime: this.lastConnectTime, //前台每次点击拨打电话储存一个时间
				orderPhone: this.phone, //预约号码
				orderStatus: this.yes, //是否预约：Y-是；N-否
				serviceResult1: this.picketData[0][this.multiIndex[0]].name,
				serviceType1: this.picketData[0][this.multiIndex[0]].id,
				validStatus: 3 //拨打状态，3为已拨打
			}
			//判断服务类型传的参数
			if(this.picketData[1][this.multiIndex[1]]){
				post_data.serviceResult2 = this.picketData[1][this.multiIndex[1]].name
				post_data.serviceType2 = this.picketData[1][this.multiIndex[1]].id
				
				if(this.picketData[2][this.multiIndex[2]]){
					post_data.serviceResult3 = this.picketData[2][this.multiIndex[2]].name
					post_data.serviceType3 = this.picketData[2][this.multiIndex[2]].id
				}

			}
			if(this.yes == 'Y'){
				post_data.orderTime = this.date +' '+this.time //预约时间
			}
			
			uni.request({
				method: 'GET',
				url: this.apiDomian + '/batchTask/update',
				data: post_data,
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					'Authorization': 'Arch6WithCloud '+self.uerInfo.token
				}
			})
			.then((data) => {
				// 请求后
				
				uni.showToast({
				    title: '提交成功',
				    duration: 2000,
				    success: function () {
				      setTimeout(function() {
				        uni.redirectTo({
				          url: '/pages/home/task/task',
				        })
				      }, 1000);
				    }
				});
				
			})
		},
		
		// 组件传的更新值
		getDate(data){
			this.questionObj = data
		},
		
		//切换radio选项
		changeYes(key) {
			this.yes = key
			this.getDatimes()//默认预约显示的年月日
			this.getTimes()//默认预约显示的时分秒
		},

		// 选择日期
		bindDateChange(e) {
			this.date = e.detail.value
		}

	}


}
